@import '../../styles/index.scss';

.header {
    width: $with-1920;
    padding: 28px 0;
    background-color: rgb(144, 148, 148);

    .container {
        margin: 0 auto;
        width: $container-width-1920;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .logo {
            display: block;
            width: 120px;
            height: 24px;
            background-image: url('image/logo.svg');
        }
    
        .nav {
            display: flex;
            align-items: center;
            gap: 24px;
            @include body16($font-family, $font-weight-regular);
            color: $white-color;
            
            .link {
                cursor: pointer;
                &:hover {
                   text-decoration: underline;
                   text-underline-offset: 4px;
                } 
            }
        }
    }
}